@import url(http://fonts.cdnfonts.com/css/pokemon-solid);
h1 {
    font-family:  'Pokemon Solid', sans-serif;
    letter-spacing: .2rem;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: thistle;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.Pokecard {
    width:  250px;
    margin: 20px 25px;
    padding-bottom: 10px;
    text-align: center;
    background-color: white;
    box-shadow: 0px 10px 15px 2px #636363;
    border-radius: 5px;
    transition: 1.25s;
}

.Pokecard:hover {
    -webkit-transform: translate(0px, -10px) scale(1.05);
            transform: translate(0px, -10px) scale(1.05);
}
.Pokecard-image-container {
    background-color: lightgrey;
    margin: 10px;
    border-radius: 5px;
}

.Pokecard-image {
    transition: 1.25s;
}

.Pokecard:hover > * .Pokecard-image {
    -webkit-transform: translate(0px, -10px) scale(1.25);
            transform: translate(0px, -10px) scale(1.25);
}
.Pokedex {
    text-align: center;
}

.Pokedex-cards {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.Pokedex-winner {
    color: green ;
    
}

.Pokedex-loser {
    color: red;
}


